import React, { useState, Fragment } from 'react';
import classes from './quest.module.css';
import bg from './5.png'
import Game from '../Game';
import ReactCSSTransitionGroup from "react-addons-css-transition-group";
const Quest5 = (props) => {
    const [accepted, setAccepted] = useState(false);
    const dialogues = [
        { line: `Wow! You're really good at this game. Can you guess where is the gift you aim?`, complete: false },
        { line: `It is hidden in a place where you don't usually go. Together with old and new stuff it lay low.`, complete: false },
        { line: `Behind its secret door are lot of boxes. One of them is huge and seethes in darkness.`, complete: false }, 
        { line: `The moogles are all excited to see what it will bring. Get a hold of it and soon you'll be the KING!`, complete: false },
        { line: 'last', complete: true },
      ];
      const answer="I found it!";
    return ( <Fragment>
        <ReactCSSTransitionGroup
       component="div"
       transitionName="example"
       transitionAppear={true}
       transitionAppearTimeout={500}
       transitionEnter={false}
       transitionLeave={false}>
        <div>
            {accepted ? (<div>
                <Game startMusic={true} dialogues={dialogues} answer={answer} next="/final"/>
            </div>)
                :
                <div className={classes.Container}>
                    <span onClick={() => setAccepted(true)}><p className={classes.Name}>Accept</p></span>
                    <p className={classes.Dialogue}>Decline</p>
                    <img src={bg} />
                </div>
            }
        </div>
        </ReactCSSTransitionGroup>
        </Fragment>
    )
}
export default Quest5