import React, { useState, Fragment } from 'react';
import classes from './quest.module.css';
import bg from './3.png'
import Game from '../Game';
import ReactCSSTransitionGroup from "react-addons-css-transition-group";
const Quest3 = (props) => {
    const [accepted, setAccepted] = useState(false);
    const dialogues = [
        { line: `I know it's really easy on the roses. So you think it's all but multiple choices?`, complete: false },
        { line: `Well you got it wrong. Let's make our journey long.`, complete: false },
        { line: `Life is full of ups and downs, and it goes round and round.`, complete: false },
        { line: `For you to reach a destination, it must be in a good condition.`, complete: false },
        { line: `Do you get where the treasure lie? Now get it, or say goodbye!`, complete: false },
        { line: '', complete: true },
      ];
      const answer="121818";
    return (
        <Fragment>
        <ReactCSSTransitionGroup
       component="div"
       transitionName="example"
       transitionAppear={true}
       transitionAppearTimeout={500}
       transitionEnter={false}
       transitionLeave={false}>
        <div>
            {accepted ? (<div>
                <Game startMusic={true} dialogues={dialogues} answer={answer} next="/quest4"/>
            </div>)
                :
                <div className={classes.Container}>
                    <span onClick={() => setAccepted(true)}><p className={classes.Name}>Accept</p></span>
                    <p className={classes.Dialogue}>Decline</p>
                    <img src={bg} />
                </div>
            }
        </div>
        </ReactCSSTransitionGroup>
        </Fragment>
    )
}
export default Quest3