import React, { useState , Fragment } from 'react';
import classes from './quest.module.css';
import bg from './2.png'
import Game from '../Game';
import ReactCSSTransitionGroup from "react-addons-css-transition-group";
const Quest2 = (props) => {
    const [accepted, setAccepted] = useState(false);
    const dialogues = [
        { line: `So take a chill pill, don't you feel the thrill?`, complete: false },
        { line: `This one is very easy - effortless, obvious, EASY!`, complete: false },
        { line: `The clue is entangled with an invisible root. Think very carefully, think of finding the loot.`, complete: false },
        { line: `Now go to the nest and uproot the roses, if you want yourself to have an access.`, complete: false },
        { line: '', complete: true },
      ];
      
      const answer="For the freedom of all!";
    return (
        <Fragment>
        <ReactCSSTransitionGroup
       component="div"
       transitionName="example"
       transitionAppear={true}
       transitionAppearTimeout={500}
       transitionEnter={false}
       transitionLeave={false}>
        <div>
            {accepted ? (<div>
                <Game startMusic={true} dialogues={dialogues} answer={answer} next="/quest3" choices={true} />
            </div>)
                :
                <div className={classes.Container}>
                    <span onClick={() => setAccepted(true)}><p className={classes.Name}>Accept</p></span>
                    <p className={classes.Dialogue}>Decline</p>
                    <img src={bg} />
                </div>
            }
        </div>
        </ReactCSSTransitionGroup>
        </Fragment>
    )
}
export default Quest2