import React , {Suspense} from 'react';
import { render } from 'react-dom';

import Loader from "react-loaders";
// import componentns
import Game from './Game';
import Welcome from './Welcome';

// import react router
import { BrowserRouter, Route, Redirect } from 'react-router-dom'
import Quest1 from './Quests/quest1';
import Quest2 from './Quests/quest2';
import Quest3 from './Quests/quest3';
import Quest4 from './Quests/quest4';
import Quest5 from './Quests/quest5';
const App = () => (
  <BrowserRouter>
    <div className="App">
    <Suspense
        fallback={
          <div className="loader-container">
            <div className="loader-container-inner">
              <div className="text-center">
                <Loader type="ball-scale-multiple" />
              </div>
              <h6 className="mt-3">
                {" "}
                <br />
                Please wait...
              </h6>
            </div>
          </div>
        }>
      <Route path="/welcome" component={Welcome} />
  
      <Route path="/game" component={Game} />
      <Route path="/quest1" component={Quest1} />
      <Route path="/quest2" component={Quest2} />
      <Route path="/quest3" component={Quest3} />
      <Route path="/quest4" component={Quest4} />
      <Route path="/quest5" component={Quest5} />
      <Redirect from="/" to="/welcome" />
      </Suspense>
    </div>
  </BrowserRouter>
);
export default App;