import React, { useState,Fragment } from "react";
import logo from './e.PNG';
import './App.css';
import { Button, Modal, ModalBody } from "reactstrap"
import Game from "./Game"

import ReactCSSTransitionGroup from "react-addons-css-transition-group";
const App = (props) => {
  const [yes, setyes] = useState(false);
  const [no, setno] = useState(false);
  const module = { component: Game, path: "/game" };
  const dialogues = [
    { line: 'Warrior of Love... Hear... Feel... Think...', complete: false },
    { line: 'Like the rain from a cloudless sky, a gift from thy beloved will be yours. Holding it is nigh!', complete: false },
    { line: 'Complete these quests and thou shall be rewarded. Assay to fail and thou will rend mine heart assunder.', complete: true }
  ];
  return (
    <Fragment>
  <ReactCSSTransitionGroup
    component="div"
    transitionName="example"
    transitionAppear={true}
    transitionAppearTimeout={500}
    transitionEnter={false}
    transitionLeave={false}>
    {yes === false ?
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" style={{ height: 100, width: 100 }} />
        <br />
        <p><h1>
         HAPPY BIRTHDAY!</h1>
        </p>
        <label>Are you ready for your surprise?</label>
        <div className="d-flex">
          <Button className="font-weight-bold ml-2" color="light" onClick={() => setyes(true)}>Yes</Button>
          <Button className="font-weight-bold ml-2" color="dark" onClick={() => setno(true)}>No</Button>
        </div>
      </header>

      <Modal
        backdrop="static"
        fade={false}
        keyboard={false}
        isOpen={no}
        centered
      >
        <ModalBody>
          <span className="text-center font-weight-bold">
            <h5>
              <strong>{props.title}</strong>
            </h5>
            <p>Sabi ko na nga ba gagawin mo to! Pwes, wala kang choice. BLEH!</p>
          </span>

          <div className="d-flex justify-content-end mt-3">
            <Button
              disabled={props.remarks === ""}
              className="btn ml-2 btn-wide font-weight-bold"
              color="dark"
              type="submit"
              onClick={() => setyes(true)}
            >
              OK
          </Button>
          </div>
        </ModalBody>
      </Modal>
    </div>
      :
    <Game startMusic={true} dialogues={dialogues} next="/quest1" />}
  </ReactCSSTransitionGroup>
  </Fragment>
  );
}

export default App;
