import React, { useState, useEffect, Fragment } from 'react';
import ReactCSSTransitionGroup from "react-addons-css-transition-group";
import bgVid from './converse.mp4'
import classes from './bgvideo.module.css';
import box from './box.png';
import bgSound from './homestead.mp3'
import soundComplete from './complete.mp3'
import { Redirect } from 'react-router-dom';
import comp from './quest_complete.PNG'
import Final from './Final';
const audio = new Audio(bgSound);
const Game = (props) => {

  const [audioComplete] = useState(new Audio(soundComplete));
  const videoSource = bgVid;
  const [ix, setIx] = useState(0);
  const [isComplete, setIsComplete] = useState(false);
  const [isCorrect, setIsCorrect] = useState(false);
  const [isCorrect2, setIsCorrect2] = useState(false);
  const [answer, setAnswer] = useState('');

  useEffect(() => {

    audio.addEventListener('ended', (event) => {
      console.log('audio has been ended');
    });
    audioComplete.addEventListener('ended', (event) => {
      console.log('audio has been ended');
      setIsCorrect(false);
      setIsComplete(true);
    });
    if (props.startMusic === true) {
      audio.addEventListener('ended', audio.play());
      audio.play();
    }
  }, []);

  // useEffect(() => {
  //   // if (timer === 0 && isCorrect) {
  //   //   audio.addEventListener('ended', audio.play());
  //   //   audio.play();
  //   // }
  //  if(isCorrect) setTimer(timer+1);
  //   if (timer === 2000) {
  //     setIsCorrect(false);
  //     setTimer(0);
  //   }
  // }, [timer, isCorrect]);

  useEffect(() => {
    if (isCorrect) {
      audio.pause();
      audioComplete.play();
    }
  }, [isCorrect]);

  useEffect(() => {
    if (isCorrect2) {
      audio.pause();
    }
  }, [isCorrect2]);


  function next() {
    console.log(props.dialogues[ix].complete)
    if (props.dialogues[ix].complete) {
      audio.pause();
      audio.removeEventListener('ended', audio.play());
      setIsComplete(true);
    }
    else
      setIx(ix + 1);
  }
  function checkAnswer(answer) {
    if (props.answer === answer)
      setIsCorrect(true)
    else
      setIx(0);
  }
  function checkAnswer2(answer) {
    if (props.answer === answer)
      setIsCorrect2(true)
    else
      setIx(0);
  }
  return (
    <Fragment>
      <ReactCSSTransitionGroup
        component="div"
        transitionName="example"
        transitionAppear={true}
        transitionAppearTimeout={500}
        transitionEnter={false}
        transitionLeave={false}>
        {isComplete ? <Redirect to={props.next} />
          :
          props.dialogues[ix].line === '' ?
            isCorrect ?
              <div className={classes.Container2}>
                <img src={comp} />
              </div> :
              <div className={classes.Container1} >
                <div className={classes.Say}>
                  <p className={classes.Name1}>What will you say?</p>
                  {props.choices ?
                    <p className={classes.Dialogue1}>
                      <button className={classes.Button} onClick={(e) => { checkAnswer(e.target.outerText) }}>Come hells or high water!</button>
                      <button className={classes.Button} onClick={(e) => { checkAnswer(e.target.outerText) }}>For the freedom of all!</button>
                      <button className={classes.Button} onClick={(e) => { checkAnswer(e.target.outerText) }}>To infinity and beyond!</button>
                    </p>
                    :
                    <p className={classes.Dialogue1}>
                      <input value={answer} className="form-control"
                        onChange={(e) => setAnswer(e.target.value)} />
                      <br />
                      <button className={classes.Button1} color="light" onClick={() => checkAnswer(answer)}>I entered my answer above</button>

                    </p>

                  }
                </div>
              </div>

            :
            (
              props.dialogues[ix].line === 'last' ?
                (isCorrect2 ? 
               <Final startMusic={true}/>
                  :
                  <div className={classes.Container1} >
                    <div className={classes.Say}>
                      <p className={classes.Name1}>What will you say?</p>

                      <p className={classes.Dialogue1}>
                        <button className={classes.Button} onClick={(e) => { checkAnswer2(e.target.outerText) }}>I found it!</button>
                        <button className={classes.Button} onClick={(e) => { checkAnswer2(e.target.outerText) }}>Wait, I'm a little lost.</button>
                      </p>
                    </div>
                  </div>)
                :
                <div className={classes.Container} >
                  <video autoPlay="autoplay" loop="loop" muted className={classes.Video} >
                    <source src={videoSource} type="video/mp4" />
                  </video>

                  <div className={classes.Content}>
                    <div className={classes.SubContent} >
                      <p className={classes.Name}>Sonjhe</p>
                      <span onClick={() => { next() }}>
                        <p className={classes.Dialogue}>
                          {props.dialogues[ix].line}
                        </p>
                        <img src={box} />

                      </span>
                    </div>
                  </div>
                </div>)}
      </ReactCSSTransitionGroup>
    </Fragment>
  )
}
export default Game;
