import React, { useState, Fragment } from 'react';
import classes from './quest.module.css';
import bg from './4.png'
import Game from '../Game';
import ReactCSSTransitionGroup from "react-addons-css-transition-group";
const Quest4 = (props) => {
    const [accepted, setAccepted] = useState(false);
    const dialogues = [     
        { line: `Arrgh! This time you don't have to go to a place. You just have to think hard on this phase:`, complete: false },
        { line: `Every night when most of the people are asleep, two hungry goobbues look for something to eat.`, complete: false },
        { line: `A light meal or something high in calorie, to maintain their mood and big fluffy body.`, complete: false },
        { line: `This is the last meal of the day but they eat it earlier, by the way.`, complete: false },
        { line: '', complete: true },
      ];
      const answer="midnight snack";
    return ( <Fragment>
        <ReactCSSTransitionGroup
       component="div"
       transitionName="example"
       transitionAppear={true}
       transitionAppearTimeout={500}
       transitionEnter={false}
       transitionLeave={false}>
        <div>
            {accepted ? (<div>
                <Game startMusic={true} dialogues={dialogues} answer={answer} next="/quest5"/>
            </div>)
                :
                <div className={classes.Container}>
                    <span onClick={() => setAccepted(true)}><p className={classes.Name}>Accept</p></span>
                    <p className={classes.Dialogue}>Decline</p>
                    <img src={bg} />
                </div>
            }
        </div>
        </ReactCSSTransitionGroup>
        </Fragment>
    )
}
export default Quest4