import React, { useState, Fragment } from 'react';
import classes from './quest.module.css';
import bg from './1.png'
import Game from '../Game';
import ReactCSSTransitionGroup from "react-addons-css-transition-group";
const Quest1 = (props) => {
    const [accepted, setAccepted] = useState(false);
    const dialogues = [
        { line: 'When you feel down and lost, you must pray at any cost.', complete: false },
        { line: 'For the divine mercy will guide you, to what the holy family will give you.', complete: false },
        { line: `Don't be sad, don't feel blue. Now go to the place yonder and tell me what's the clue.`, complete: false },
        { line: '', complete: true },
    ];
    const answer = "Come hells or high water!";
    return (
        <Fragment>
            <ReactCSSTransitionGroup
                component="div"
                transitionName="example"
                transitionAppear={true}
                transitionAppearTimeout={500}
                transitionEnter={false}
                transitionLeave={false}>

                <div>

                    {accepted ? (<div>
                        <Game startMusic={true} dialogues={dialogues} answer={answer} next="/quest2" choices={true} />
                    </div>)
                        :
                        <div className={classes.Container}>
                            <span onClick={() => setAccepted(true)}><p className={classes.Name}>Accept</p></span>
                            <p className={classes.Dialogue}>Decline</p>
                            <img src={bg} />
                        </div>
                    }

                </div>
            </ReactCSSTransitionGroup>
        </Fragment>)
}
export default Quest1